import React, { Component } from "react";
import DatePicker from "react-date-picker";
import Select from "react-select";
import axios from "axios";
import {optionsPagesa} from "../../variables/Modal"
import {menyrePagese} from "../../variables/Modal"
import {kodeProdukte} from "../../variables/Modal"
import moment from "moment";
import HmacSHA256 from "crypto-js/hmac-sha256";


class RaportPodePadorezuar extends Component {
  state = {
    PNga: new Date(),
    PDeri: new Date(),
    date: new Date(),
    posts: [{}],
    username: window.UserP.username,
    dataPosts: [],
    Id: "",
    perdorues: "",
    terminal: [],
    data2: [],
    data3: [],
    txtAgjensi: true,
    checkedboxKP: false,
    Agjensi: window.UserP.agencyId,
    data4: [],
    data5: [],
    data6: [],
    ProcesorId: "",
    selectedOption: null,
    KushPaguan: optionsPagesa[2],
    KodiProduktit: kodeProdukte[4],
    MenyrePagese: { value: "True", label: "Te Gjitha" },
    KodLevizje: { value: "True", label: "Te Gjitha" },
    KlienteSubjekte: { value: "True", label: "Te Gjitha" },
    Pershkrimi: { value: "True", label: "Te Gjitha" },
    Origjina: { value: "True", label: "Te Gjitha" },
    
  };

  handleClear = () => {
    this.setState({
      date: "",
      pNga: new Date(),
      pDeri: new Date(),
      posts: [{}],
      terminal: [],
    });
  };
  handleInputChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    this.setState({ [name]: value.toUpperCase() });
  };
  componentDidMount() {
    {
      fetch(window.UserP.url + "POD/GetPerdorues", {
        method: "Post",
        headers: {
          Accept: "application/json",
          "Content-type": "application/json",
          "Access-Control-Allow-Origin": "*",
        },
        body: JSON.stringify({
          Token: HmacSHA256(
            Math.round(new Date().getTime() / 1000).toString(),
            window.UserP.key
          ).toString(),
           username: this.state.username,
          Id: window.UserP.agencyId,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ data2: data }));
    }
    {
      fetch(window.UserP.url + "POD/GetKodeLevizje", {
        method: "Post",
        headers: {
          Accept: "application/json",
          "Content-type": "application/json",
           "Access-Control-Allow-Origin": "*"
        },
        body: JSON.stringify({
          Token: HmacSHA256(
            Math.round(new Date().getTime() / 1000).toString(),
            window.UserP.key
          ).toString(),
           username: this.state.username,
          agjensi: window.UserP.agencyId,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ data3: data }));
    }
    {
      fetch(window.UserP.url + "POD/GetKodePershkrimi", {
        method: "Post",
        headers: {
          Accept: "application/json",
          "Content-type": "application/json",
           "Access-Control-Allow-Origin": "*"
        },
        body: JSON.stringify({
          Token: HmacSHA256(
            Math.round(new Date().getTime() / 1000).toString(),
            window.UserP.key
          ).toString(),
           username: this.state.username,
          agjensi: window.UserP.agencyId,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ data4: data }));
    }
    {
      fetch(window.UserP.url + "POD/GetKlienteSubjekte", {
        method: "Post",
        headers: {
          Accept: "application/json",
          "Content-type": "application/json",
           "Access-Control-Allow-Origin": "*"
        },
        body: JSON.stringify({
          Token: HmacSHA256(
            Math.round(new Date().getTime() / 1000).toString(),
            window.UserP.key
          ).toString(),
           username: this.state.username,
          agjensi: window.UserP.agencyId,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ data5: data }));
    }
    {
      fetch(window.UserP.url + "POD/GetAgjensi", {
        method: "Post",
        headers: {
          Accept: "application/json",
          "Content-type": "application/json",
           "Access-Control-Allow-Origin": "*"
        },
        body: JSON.stringify({
          Token: HmacSHA256(
            Math.round(new Date().getTime() / 1000).toString(),
            window.UserP.key
          ).toString(),
           username: this.state.username,
          agjensi: window.UserP.agencyId,
        }),
      })
        .then((res) => res.json())
        .then((data) => this.setState({ data6: data }));
    }
  }
  handleChangeCheckKP = (e) => {
    const { checked } = e.target
    this.setState({
      checkedboxKP: checked,
    })
    this.state.checkedboxKP = checked;
  };
  convertToDropDown = (par) => {
    var ret = [];
    var paramArray = par;

    for (var i in paramArray) {
      var objItem = { value: "", label: "" };
      objItem.value = paramArray[i];
      objItem.label = paramArray[i];
      ret.push(objItem);
    }

    return ret;
  };
  printfunction = (post) => {
    var windowName = "";
    var windowUrl = " ";
    var myWindow = window.open(
      windowUrl,
      windowName,
      "left=0,top=0,right=0,bottom=0 margin=0,0,0,0"
    );

    let test = this.state.posts;
    var test5 =
      "&nbsp" +
      // "<table >" +
      "<tr>" +
      "<th >" +
      "Agjensia: " +
      this.state.Agjensi +
      "<br>" +
      "<br>" +
      "</th>" +
      "<br>" +
      "<br>" +
      "<font face='calibri'" +
      ">" +
      "<br>" +
      "&nbsp" +
      "&nbsp" +
      "<br>" +
      "<br>" +
      "<b>" +
      "   </tr>" +
      "<table border='1' >" +
      "<thead>" +
      "<tr>" +
      "<th class='cell'>" +
      "Nr_POD" +
      "</th>" +
      " <th class='cell'>" +
      "Arsye Mosdorezimi" +
      "</th>" +
      "<th class='-cell'>" +
      "Origjina" +
      "</th>" +
      "<th class='-cell'>" +
      "Data" +
      "</th>" +
      "<th class='-cell'>" +
      "M.Pagese" +
      "</th>" +
      "<th class='-cell'>" +
      "Kush Paguan" +
      "</th>" +
      "<th class='-cell'>" +
      "Derguesi" +
      "</th>" +
      "<th class='-cell'>" +
      "Vlera KP" +
      "</th>" +
      "<th class='-cell'>" +
      "Mon.KP" +
      "</th>" +
      "<th class='-cell'>" +
      "Totali" +
      "</th>" +
      "  </tr>" +
      "  </thead>" +
      "  <tbody>";

    for (var i = 0; i < test.length; i++) {
      let monedha="";
      if(test[i].Monedha_Kunder_Pagese =="5"){
        monedha = "LEKE";
      }else{
        monedha = "EURO";
      }
      test5 +=
        "<tr class='firstRow'>" +
        "<td class='Nr_POD:-cell'>" +
        test[i].Nr_POD +
        "</td>" +
        " <td class='Arsye Mosdorezimi:-cell'>" +
        test[i].Arsye_Mosdorezimi +
        "</td>" +
        " <td class='Origjina:-cell'>" +
        test[i].Origjina +
        "</td>" +
        " <td class='Data:-cell'>" +
        moment(test[i].Data).format("DD/MM/YYYY") +
        "</td>" +
        " <td class='M.Pagese:-cell'>" +
        test[i].Menyre_Pagese +
        "</td>" +
        " <td class='Kush Paguan:-cell'>" +
        test[i].Kush_Paguan +
        "</td>" +
        " <td class='Derguesi:-cell'>" +
        test[i].Derguesi +
        "</td>" +
        " <td class='Vlera KP:-cell'>" +
        test[i].Vlera_Kunder_Pagese +
        "</td>" +
        " <td class='Mon.KP:-cell'>" +
        monedha +
        "</td>" +
        " <td class='Totali:-cell'>" +
        test[i].Totali +
        "</td>" +
        " </tr>" +
        "&nbsp" +
        "&nbsp" +
        "   </tr>";
    }

    test5 +=
      "<th >" +
      "Total Kundrejt Pagese Leke : " +
      "0.2" +
      "<br>" +
      "<br>" +
      "</th>" +
      "<th >" +
      "Total Kundrejt Pagese Euro: " +
      "0.1" +
      "<br>" +
      "<br>" +
      "</th>" +
      "  </tbody>" +
      "</table>" +
      "</font>";

    myWindow.document.write(test5);

    myWindow.focus();
    myWindow.print();
  };

  handleClick = (e) => {
    const RaportReq = {
      Origjina: this.state.Origjina.label,
      KushPaguan: this.state.KushPaguan.label,
      KodLevizje: this.state.KodLevizje.label,
      MenyrePagese: this.state.MenyrePagese.label,
      Agjensi: window.UserP.agencyId,
      KlienteSubjekte: this.state.KlienteSubjekte.label,
      KodiProduktit: this.state.KodiProduktit.label,
      Pershkrimi: this.state.Pershkrimi.label,
      PNga: this.state.PNga.toLocaleDateString(),
      PDeri: this.state.PDeri.toLocaleDateString(),
      KundrejtPageseCheck: this.state.checkedboxKP,
      username: this.state.username,
      Token: HmacSHA256(
        Math.round(new Date().getTime() / 1000).toString(),
        window.UserP.key
      ).toString(),
    };
  
    axios
      .post(window.UserP.url + "POD/LogPodPaDorezuar", RaportReq)
      .then((response) => {
        this.setState({
          posts: response.data,
        });
        this.printfunction(this.state.posts);
        // let valueAdded = 0;
        // let vleraPakos = 0;
        // let totalLek = 0;

        // for (var i = 0; i < this.state.posts.length; i++) {
        //   //count1 = parseInt(this.state.posts[i].Cmimi_Baze);
        //   valueAdded += parseFloat(this.state.posts[i].Cmimi_Baze);
        //   vleraPakos += parseFloat(this.state.posts[i].VleraKP);
        // }
        // totalLek = valueAdded + vleraPakos;
        // this.setState({
        //   TotalVP: this.thousands_separators(valueAdded.toFixed(2)),
        //   TotalKP: this.thousands_separators(vleraPakos.toFixed(2)),
        //   TotalLek: this.thousands_separators(totalLek.toFixed(2))
        // });
      
      });
  };
  handleChange = (value) => {
    this.setState({
      PNga: value,
    });
  };

  handleChange1 = (value) => {
    this.setState({
      PDeri: value,
    });
  };
  onChangeFunc = (name) => (value) => {
    // console.log(value.label);
    this.setState({
      [name]: value,
    });
  };

  render() {
    return (
      <div className="Form2">
        <div className="card card-body bg-light col-md-12">
          <div className="row ">
            <div className="col-md-2">
              <label className="">Mos Dorezuar nga:</label>
            </div>
            <div className="col-md-2">
              <input
                className="form-control input-sm"
                value={this.state.Agjensi}
                type="text"
                //   onChange={e => this.handleInputChange(e, "Agjensi")}
                disabled={this.state.txtAgjensi}
              />
            </div>
            <div className="col-md-2">
              <label>Origjina</label>
            </div>
            <div className="col-md-2">
              <Select
                onChange={this.onChangeFunc("Origjina")}
                options={this.convertToDropDown(this.state.data6)}
                defaultValue={{ value: "True", label: "Te Gjitha" }}
              />
            </div>
            <div className="col-md-2">
              <label>Kush Paguan</label>
            </div>
            <div className="col-md-2">
              <Select
                className=""
                value={this.state.KushPaguan}
                onChange={this.onChangeFunc("KushPaguan")}
                options={optionsPagesa}
                defaultValue={optionsPagesa[2]}
              />
            </div>
          </div>
          <div className="row mt-1">
            <div className="col-md-2">
              <label className="">Kodi Produktit</label>
            </div>
            <div className="col-md-2">
              <Select
                className="custom-Select "
                onChange={this.onChangeFunc("KodiProduktit")}
                options={kodeProdukte}
                defaultValue={kodeProdukte[4]}
              />
            </div>
            <div className="col-md-2">
              <label>Kodi Levizjes</label>
            </div>
            <div className="col-md-2">
              <Select
                className=""
                onChange={this.onChangeFunc("KodLevizje")}
                options={this.convertToDropDown(this.state.data3)}
                defaultValue={{ value: "True", label: "Te Gjitha" }}
              />
            </div>
            <div className="col-md-2">
              <label>Kliente Subjekte</label>
            </div>
            <div className="col-md-2">
              <Select
                className="custom-Select"
                //value={this.state.kushPaguan}
                onChange={this.onChangeFunc("KlienteSubjekte")}
                options={this.convertToDropDown(this.state.data5)}
                defaultValue={{ value: "True", label: "Te Gjitha" }}
              />
            </div>
          </div>
          <div className="row mt-1">
            <div className="col-md-2">
              <label className="">Pershkrimi</label>
            </div>
            <div className="col-md-2">
              <Select
                className="custom-Select"
                //value={this.state.kushPaguan}
                onChange={this.onChangeFunc("Pershkrimi")}
                options={this.convertToDropDown(this.state.data4)}
                defaultValue={{ value: "True", label: "Te Gjitha" }}
              />
            </div>
            <div className="col-md-2">
              <label>Menyre Pagese</label>
            </div>
            <div className="col-md-2">
              <Select
                className="custom-Select"
                onChange={this.onChangeFunc("MenyrePagese")}
                options={menyrePagese}
                defaultValue={menyrePagese[2]}
              />
            </div>
          </div>
          <div className="row">
            <div className="col-md-4">
              {' '}
            </div>
            <div className="col-md-2">
             <label className="mt-3">KP(Leke)</label>
            </div>
            <div className="col-md-1 mt-2">
                              <label class="checkboxcontainer mt-sm-1 ml-md-1">
                           <input
                              id="26"
                              type="checkbox"
                              name="test"
                              onChange={e =>this.handleChangeCheckKP(e)}
                              defaultChecked={this.state.checkedboxKP}
                              
                            />
                            <span class="checkmark"></span>
                            </label> 
                            </div>
          </div>
        
          <div className="row mt-5">
          <div className="card card-body bg-light col-md-12">
            <div className="row"> 
            <div className="col-md-3 ">
              <label className="">Nga</label>
            </div>
            <div className="col-md-3 ">
              <DatePicker
                className="float-left"
                onChange={this.handleChange}
                value={this.state.PNga}
                name="PNga"
                dateFormat="Pp"
              />
            </div>

            <div className="col-md-3">
              <label className="">Deri</label>
            </div>
            <div className="col-md-3">
              <DatePicker
                onChange={this.handleChange}
                value={this.state.PDeri}
                name="PDeri"
                dateFormat="Pp"
              />
            </div>
          </div></div></div>
          <div className="row mt-4">
            <div className="col-md-6">
              <button
                onClick={() => this.handleClick()}
                className="btn btn-primary "
              >
                <span data-notify="icon" className="pe-7s-print" />
                &nbsp; Printo
              </button>
            </div>
            <div className="col-md-6">
              <button
                className="btn btn-primary float-right"
                // type="submit"
                onClick={this.handleClear}
              >
                <span data-notify="icon" className="pe-7s-trash" />
                &nbsp; Pastro
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default RaportPodePadorezuar;
