  handleChange = (value) => {
    this.setState({
      PNga: value,
    });
  };

  handleChange1 = (value) => {
    this.setState({
      PDeri: value,
    });
  };


  handleClickGjurmo = (e) => {
    this.showModal();
    var d = new Date();
    const data = {
      nrKodi: e,
      Token: HmacSHA256(
        Math.round(new Date().getTime() / 1000).toString(),
        window.UserP.key
      ).toString(),

      username: window.UserP.username,
    };

    const headers = {
      Accept: "application/json",
      "Content-type": "application/json",
      "Access-Control-Allow-Origin": "*",
    };

    axios.post(window.UserP.url + "POD/Gjurmo", data).then((response) => {
      this.setState({ dataPosts: response.data });
    });
  };


  handleClick = (e) => {
    const RaportReq = {
      PNga: this.state.PNga.toLocaleDateString(),
      PDeri: this.state.PDeri.toLocaleDateString(),
      Token: HmacSHA256(
        Math.round(new Date().getTime() / 1000).toString(),
        window.UserP.key
      ).toString(),
      Perdorues: this.state.PerdoruesAgjensi.label,
      ProcesorId: window.UserP.idProcesori,
      Agjensi: window.UserP.agencyId,
      PerdoruesAgjensi: this.state.PerdoruesAgjensi.label,
      Terminal: window.UserP.terminalId,
      KodLevizje: this.state.KodLevizje.label,
      Pershkrimi: this.state.Pershkrimi.label,
      KodiProduktit: this.state.KodiProduktit.label,
      KushPaguan: this.state.KushPaguan.label,
      MenyrePagese: this.state.MenyrePagese.label,
      Dergues: this.state.Dergues.label,
      KlienteSubjekte: this.state.KlienteSubjekte.label,
      NrPod: this.state.NrPod,
      Destinacion: this.state.Destinacion.label,
      Korrier: this.state.Korrier.label,
      StatusPodDirection: this.state.StatusPodDirection,
      KundrejtPageseCheck: this.state.checkedboxKP,
      username: this.state.username,
    };
    
    this.setState({ loading: true }, () => {
      axios
        .post(window.UserP.url + "POD/LogDorezimeArkaAgjent", RaportReq)
        .then((response) => {
          this.setState({
            loading: false,
            posts: response.data,
          });
          let valueAdded = 0;
          let vleraPakosLeke = 0;
          let vleraPakosEuro = 0;
          let totalLek = 0;
          let totShtese = 0;
          let totTakse = 0;
          let totSig = 0;
          let totTvsh = 0;
          let total = 0;

          for (var i = 0; i < this.state.posts.length; i++) {
            if (this.state.posts[i].Monedha_Kunder_Pagese == "LEKE") {
              vleraPakosLeke += parseFloat(
                this.state.posts[i].Vlera_Kunder_Pagese
              );
            } else {
              vleraPakosEuro += parseFloat(
                this.state.posts[i].Vlera_Kunder_Pagese
              );
            }
            totShtese += parseFloat(this.state.posts[i].Shtesa);
            totTakse += parseFloat(this.state.posts[i].Takse_Karburant_Leke);
            totSig += parseFloat(this.state.posts[i].Vl__Sig_);
            totTvsh += parseFloat(this.state.posts[i].Tvsh);
            valueAdded += parseFloat(this.state.posts[i].Cmimi_me_zbritje);
            total += parseFloat(this.state.posts[i].Totali);
          }

          this.setState({
            KPLeke: thousands_separators(vleraPakosLeke.toFixed(2)),
            KPEuro: thousands_separators(vleraPakosEuro.toFixed(2)),
            TotShtese: thousands_separators(totShtese.toFixed(2)),
            TotTakse: thousands_separators(totTakse.toFixed(2)),
            TotSig: thousands_separators(totSig.toFixed(2)),
            TotTvsh: thousands_separators(totTvsh.toFixed(2)),
            TotCmimiBaze: thousands_separators(valueAdded.toFixed(2)),
            TotTotali: thousands_separators(total.toFixed(2)),
          });
        }).catch((error) => {
          this.setState({
            loading: false,
          });
          alert("Ju lutem kontaktoni departamentin e IT!");
        });
       
      // let valueAdded = 0;
      // let vleraPakos = 0;
      // let totalLek = 0;

      // for (var i = 0; i < this.state.posts.length; i++) {
      //   //count1 = parseInt(this.state.posts[i].Cmimi_Baze);
      //   valueAdded += parseFloat(this.state.posts[i].Cmimi_Baze);
      //   vleraPakos += parseFloat(this.state.posts[i].VleraKP);
      // }
      // totalLek = valueAdded + vleraPakos;
      // this.setState({
      //   TotalVP: this.thousands_separators(valueAdded.toFixed(2)),
      //   TotalKP: this.thousands_separators(vleraPakos.toFixed(2)),
      //   TotalLek: this.thousands_separators(totalLek.toFixed(2))
      // });
    })
  };
